import React from 'react';

export default class Logo extends React.Component {

    render(){
        return(
            <div className="logo">
                <picture>
                    <source media="(max-width: 768px)" srcSet="/img/logoS.png" />
                    <source media="(min-width: 769px)" srcSet="/img/logoL.png" />
                    <img src="/img/logoL.png" alt="Logomarca WEF Imóveis"/>
                </picture>
            </div>
        );
    }
}